<template>
  <div class="container-title" ref="TitleAll">
    <div class="container-title-icon " :style="{transform:`scaleY(${Icon})`,background:background1}" :class="{'icon-white':isWhite ,'icon-active':isthemeWhite}" ></div>
    <div class="container-title-icon-long" :style="{transform:`scaleY(${IconLong})`,background:background2,height:`${height2}rem`}" :class="{'icon-long-active':isthemeWhite}"></div>
    <div class="container-title-text1 container-title-text-up" :style="{opacity:TextOpacity ,transform:`translateY(${TextTranslateY})` }" :class="{'text-white':isTextWhite, 'text-active':isthemeWhite}"><em>{{title1}}</em></div>
    <div class="container-title-text2 container-title-text-down" v-if="title2isShow" :style="{opacity:TextOpacity ,transform:`translateY(${TextTranslateY})` }" :class="{'text-white':isTextWhite , 'text-active':isthemeWhite}"><em>{{title2}}</em></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Icon:0,
      IconLong:0,
      TextOpacity:0,
      TextTranslateY:`100%`,
      CliHeight:0,
      CliWidth:0
    }
  },
  methods: {
    titleScroll(){
     if(this.CliWidth<=1242){
     if(this.$refs.TitleAll.getBoundingClientRect().top >100 && this.$refs.TitleAll.getBoundingClientRect().top <(this.CliHeight+750)){
   this.Icon = 1;
   this.IconLong = 1;
   this.TextOpacity = 1;
   this.TextTranslateY = 0
 }
   }else{
     if(this.$refs.TitleAll.getBoundingClientRect().top >100 && this.$refs.TitleAll.getBoundingClientRect().top <(this.CliHeight-200)){
   this.Icon = 1;
   this.IconLong = 1;
   this.TextOpacity = 1;
   this.TextTranslateY = 0
 }
   }
    },
  },
mounted() {
 this.CliHeight = window.screen.height;
 this.CliWidth = window.screen.width;
 this.titleScroll()
 document.addEventListener('scroll',this.titleScroll)
},
beforeDestroy() {
  document.removeEventListener('scroll',this.titleScroll)
},
props:{
  title1:{
    type:String,
    required:true,
    default:'你的发生的'
  },
  title2isShow:{
    type:Boolean,
    default:true
  },
  title2:{
    type:String,
    default:'的发电房'
  },
  background1:{
    type:String,
    default:'black'
  },
   background2:{
    type:String,
    default:'black'
  },
  height2:{
    type:Number,
    default:3
  },
  isWhite:{
    type:Boolean,
    default:false
  },
  isTextWhite:{
    type:Boolean,
    default:false
  },
  isthemeWhite:{
    type:Boolean,
    default:false
  }
}
}
</script>

<style scoped lang="less">
@media(min-device-width:1025px){
  .container-title{
    height: 3rem;
    width: calc(100% - 0.7rem);
    padding-left: 0.7rem;
    position: relative;
    .container-title-icon{
      width: 0.1rem;;
      height: 1.5rem;
      position: absolute;
      left: 0;
      top: 0;
      background: black;
      transform-origin: 0 0;
      transform: scaleY(0);
      transition: transform 0.5s linear;
      &::after{
        content: "";
        position: absolute;
        left: 0;
        bottom: -0.1rem;
        border-top: 0.1rem solid black;
        border-left: 0.1rem solid transparent;

      }
    }
    .icon-white{
      &::after{
       border-top: 0.1rem solid white;
      }
    }
    .container-title-icon-long{
      width: 0.05rem;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0.1rem;
      background: black;
      transform-origin: 0 0;
      transform: scaleY(0);
      transition: transform 0.5s linear;
    }
    .container-title-text1{
          width: 100%;
          height: 1.5rem;
          color: black;
          line-height: 1.5rem;
          font-weight: bold;
          font-size: 1.3rem;
          transform: translateY(100%);
          opacity: 0;
          transition: transform 0.3s linear , opacity 0.3s linear;
    }
    .container-title-text2{
          width: 100%;
          height: 1.5rem;
          color: black;
          line-height: 1.5rem;
          font-weight: bold;
          font-size: 1.3rem;
          transform: translateY(100%);
          opacity: 0;
          transition: transform 0.3s linear 0.2s, opacity 0.3s linear 0.2s;
    }
    .text-white{
      color: white;
    }
  }
}
@media(max-device-width:1024px){
  .container-title{
    height: 2.5rem;
    width: calc(100% - 0.7rem);
    padding-left: 0.7rem;
    position: relative;
    .container-title-icon{
      width: 0.1rem;;
      height: 2.4rem;
      position: absolute;
      left: 0;
      top: 0;
      background: black !important;
      transform-origin: 0 0;
      transform: scaleY(0);
      transition: transform 0.5s linear;
      &::after{
        content: "";
        position: absolute;
        left: 0;
        bottom: -0.1rem;
        border-top: 0.1rem solid black;
        border-right: 0.1rem solid transparent;

      }
    }
    .icon-active{
      background: white !important;
      &::after{
        border-top: 0.1rem solid white;
      }
    }
    .container-title-icon-long{
      display: none;
      width: 0.05rem;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0.1rem;
      background: black;
      transform-origin: 0 0;
      transform: scaleY(0);
      transition: transform 0.5s linear;
    }
    .icon-long-active{
      background: white;
    }
    .container-title-text1{
          width: 100%;
          height: 1.25rem;
          color: black;
          line-height: 1.25rem;
          font-weight: bold;
          font-size: 1rem;
          transform: translateY(100%);
          opacity: 0;
          transition: transform 0.3s linear , opacity 0.3s linear;
    }
    .container-title-text2{
          width: 100%;
          height: 1.25rem;
          color: black;
          line-height: 1.25rem;
          font-weight: bold;
          font-size: 1rem;
          transform: translateY(100%);
          opacity: 0;
          transition: transform 0.3s linear 0.2s, opacity 0.3s linear 0.2s;
    }
    .text-active{
      color: white;
    }
  }
}
</style>